const Rushou = (resolve) => {
  import ('@/components/rushou/rushou').then((module) => {
    resolve(module)
  })
}
const rushou = [{
  path: '/rushou',
  component: Rushou,
  name: 'rushow',
  meta: { title: '' }
}]
export default rushou


